import React from "react";
import { Slide, Heading, Text, CodePane } from "spectacle";

export default () => (
  <>
    <Slide backgroundColor="#0d0d0d">
      <Heading margin="0px" fontSize="50px">
        Cdigo bloqueante
      </Heading>

      <Text fontSize={30}>
        <b><i>JavaScript is a single-threaded non-blocking asynchronous concurrent language</i></b>
      </Text>

      <Text fontSize={30}>
        Podemos pensar en un thread (hilo) como un proceso que los programas utilizan para ejecutar tareas, y cada thread solo puede ejecutar una tarea a la vez. JavaScript ejecuta todo el cdigo en un solo thread (<a href="https://developer.mozilla.org/en-US/docs/Learn/JavaScript/Asynchronous/Concepts#javascript_is_single_threaded">JavaScript is single-threaded</a>), llamado <b>main thread</b>.
      </Text>

      <Text fontSize={30}>
        Todo lo que vimos hasta ahora es cdigo sincrnico. Una sentencia se ejecuta una vez que la anterior finaliza.
      </Text>

      <Text fontSize={30}>
        El cdigo sincrnico nos impone algunas limitaciones cuando lo que debemos ejecutar es costoso, ya que JS no le devuelve el control al browser y ste puede parecer congelado, imposibilitando que maneje cualquier otra accin o tarea que el usuario solicite. A esto le llamamos cdigo bloqueante.
      </Text>
    </Slide>

    <Slide backgroundColor="#0d0d0d">
      <Heading margin="0px" fontSize="50px">
        Cdigo bloqueante
      </Heading>

      <CodePane language="js">
        {`
          const result = document.querySelector("#result");
          const clickHandler = () => {
            let fecha;
            for (let i = 0; i < 20000000; i++) {
              fecha = new Date();
            }
            result.value = result.value + fecha;
          };

          document.querySelector("#startBtn").addEventListener("click", clickHandler);
        `}
      </CodePane>

      <iframe src={`${process.env.PUBLIC_URL}/snippets/class4/blockingCode.html`}></iframe>
    </Slide>

    <Slide backgroundColor="#0d0d0d">
      <Heading margin="0px" fontSize="50px">
        Web Workers
      </Heading>

      <Text fontSize={30}>
        Los <a href="https://github.com/JulianPasquale/Webpack-templating/commit/2192a8391ccdb897ee806412a513227810131135">Web Workers</a> hacen posible la ejecucin de cdigo JS en background, en un thread separado del principal. De esta forma podemos ejecutar tareas laboriosas o costosas sin bloquear completamente el navegador.
      </Text>

      <Text fontSize={30}>
        Podemos utilizar el constructor <a href="https://developer.mozilla.org/en-US/docs/Web/API/Worker/Worker">Worker</a> para crear un worker a partir de un script. Adems un worker puede invocar a otro worker.
      </Text>

      <Text fontSize={30}>
        Los workers se comunican entre ellos y tambin con el main thread a travs del pasaje de mensajes utilizando las directivas <a href="https://developer.mozilla.org/en-US/docs/Web/API/Worker/postMessage">Worker.postMessage</a> y <a href="https://developer.mozilla.org/en-US/docs/Web/API/Window/postMessage">Window.postMessage</a>
      </Text>

      <Text fontSize={30}>
        Sin embargo los workers cuentan con algunas limitaciones, como la imposibilidad de manipular el DOM y de acceder a varios atributos y mtodos de window. Adems no nos proveen una forma de esperar por un resultado, lo que suele ser una complicacin cuando alguna tarea depende de otra.
      </Text>
    </Slide>
  </>
);
